.navbar {
    background-color: rgb(54, 23, 175);
    color: white;
    font-family: sans-serif;
    display: flex;
    padding: 1.5rem 1.3rem;
    padding-inline: 0;
    height: 3vh;
    width: 100vw;
    position: fixed;
    z-index: 10;
}

.navbar a {
    text-decoration: none;
    color: white;
}

.nav-items {
    display: flex;
    flex-direction: row;
    align-self: right;
    justify-self: right;
    position: absolute;
    right: 2rem;
    margin-top: 7px;
}

.navbar h1 {
    text-shadow: 0 0 3px white;
    letter-spacing: 1.5px;
    cursor: pointer;
    padding-left: 1.3rem;
}

.nav-item {
    padding-inline: 1rem;
    list-style-type: none;
    cursor: pointer;
}

.nav-item:hover {
    transform: translatey(-5px) scale(1.1);
    transition: all .5s;
}

.display-navbar-btn {
    display: none;
}

@media (max-width:800px) {
    .nav-items {
        display: none;
    }

    .display-navbar-btn {
        display: inline-block;
        position: absolute;
        right: .6rem;
        top: .5rem;
        font-size: 1rem;
        padding: .2rem .2rem .3rem .3rem;
        border: none;
        border-radius: 1rem;
        box-shadow: 1px 1px 5px white;
        /* transform: rotate(45deg); */
    }

    .display-navbar-btn-rotate {
        transform: rotate(45deg);
    }

    .nav-item {
        padding-inline: .4rem;
    }

    .display-navbar {
        display: flex;
        flex-direction: row;
        align-self: right;
        justify-self: right;
        position: absolute;
        right: 2rem;
        margin-top: 7px;
        animation-name: display-navbar-animation;
        animation-duration: .5s;
        animation-timing-function: linear;
        animation-fill-mode: forwards;
    }

    @keyframes display-navbar-animation {
        0% {
            right: -20rem;
        }

        100% {
            right: 2rem;
        }

    }

    .hidden {
        display: none;
    }
}