#gallery {
    font-family: 'Poppins', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: linear-gradient(to right, rgb(181, 217, 235), rgb(100, 193, 230));
}

#gallery h1 {
    font-size: 2.5rem;
    text-shadow: 0 0 5px rgb(155, 157, 175);
    padding-top: 1rem;
    color: blue;
}


.gallery-container .g img {
    height: 180px;
    border-radius: 1rem;
    border-style: inset;
    box-shadow: 10px 15px 20px black;
}

.gallery-container .g img:hover {
    transition: all 1s;
    transform: translatey(50px) scale(1.5);
}

.gallery-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 3rem;
    column-gap: 2rem;
    margin: 5rem 0;

}

@media (max-width:900px) {
    .gallery-container {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }

    .gallery-container .g img {
        height: 230px;
    }

    #gallery h1 {
        font-size: 2rem;
    }

    #gallery h4 {
        font-size: 1rem;
    }
}