#aboutus img {
    height: 15rem;
    margin: 1rem 3rem;
    border-radius: 100rem;
}


.image-container {
    /* padding-left: 40px; */
    margin-top: 2rem;
}

@media (max-width:800px) {
    #aboutus img {
        height: 10rem;
        margin: 1rem 0;
    }

    .image-container {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

}