@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@700&family=Permanent+Marker&family=Poppins&display=swap');


#home {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    color: rgb(0, 0, 0);
    font-family: 'Poppins', sans-serif;
    align-items: center;
    justify-content: center;
    position: relative;
    /* animation-name: slideup; */
    animation-duration: 1s;
}

@keyframes slideup {
    0% {
        top: 50vh;
        transform: scale(0.5);
    }

    100% {
        top: 0;
        transform: scale(1);
    }

}

#home h1 {

    text-shadow: 0 0 5px rgb(69, 127, 150);
    font-family: 'Permanent Marker', cursive;
    font-size: 3rem;
    padding-bottom: .5rem;
}

#home h4 {

    text-shadow: 0 0 5px rgb(155, 157, 175);
    font-size: 1.1rem;
    padding-bottom: 4rem;
}


#home p {
    /* text-shadow: 0 0 5px rgb(255, 255, 255); */
    text-align: center;
    color: white;
    padding-bottom: 1rem;
}

#home span {
    font-weight: 900;
    color: black;
}

.btn {
    font-size: 1.2rem;
    padding: .3rem 1rem;
    margin-inline: .5rem;
    margin-top: .5rem;
    color: white;
    background-color: blue;
    border: 1px solid blue;
    border-radius: 1rem;
}

.btn:hover {
    color: blue;
    background-color: white;
}

#home::before {
    content: '';
    background-image: url(https://images.pexels.com/photos/6549916/pexels-photo-6549916.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
    width: 100vw;
    height: 100vh;
    background-size: cover;
    filter: blur(5px);
    opacity: .9;
    background-position: center;
    position: absolute;
    z-index: -1;
}

@media (max-width:450px) {
    #home h1 {
        font-size: 2rem;
    }
}