.des-grid-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
}

#why-english .img {
    background-image: url(https://i0.wp.com/introworldeducation.com/wp-content/uploads/2020/08/08082020-Why-is-English-Important.-e1596880085109.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.des-grid-container .content {
    padding: 1rem 1.5rem;
    line-height: 1.4;
    /* justify-self: center; */
    font-family: 'Poppins', sans-serif;
    background-color: rgb(202, 248, 246);
    background-image: radial-gradient(white, rgb(211, 233, 243));
}

.des-grid-container h1 {
    margin-bottom: 1rem;
    color: blue;
}

.des-grid-container span {
    font-weight: bold;
}

.des-grid-container li {
    list-style: none;
    padding-bottom: .5rem;
}

@media (max-width:800px) {
    #why-english {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: auto 40vh;
    }

    #what-we-offer {
        display: grid;
        grid-template-columns: auto;
        grid-template-rows: 40vh auto;
    }

    .des-grid-container .content {
        padding: 0rem 1.5rem;
        font-size: 12px;
        line-height: 1.4;
        font-family: 'Poppins', sans-serif;
    }
}


/* what we offer section  */

#what-we-offer .img {
    background-image: url(https://www.bankrate.com/2022/05/25154641/discover-it-student-chrome-worth-it.jpg?auto=webp&optimize=high&crop=16:9);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}


/* more query template  */

#more-query-template::before {
    content: '';
    background-image: url(https://lh3.googleusercontent.com/W8embMRZNJ9RCKGde7FN4dM874lpsCJWF2W4C_vL6kVCfbVO6tIyRk_wZhe6rO92-VQTPd-d_vy3YLcGR6WiHwT0YvCOZzRPqgJs9K7gjX9r_echSjZ_aBWTEMxlx5nZgj_gfy_9y5dFth-EJw);
    width: 100vw;
    height: 40vh;
    filter: blur(3px);
    opacity: .90;
    position: absolute;
    background-position: center;
    background-size: cover;
    z-index: -1;
}

#more-query-template {
    display: flex;
    /* width: 100vw; */
    height: 40vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#more-query-template h5 {
    color: white;
    font-family: 'Poppins', sans-serif;
    font-size: 2.2rem;
    margin-bottom: 1rem;
    text-shadow: 0 0 5px black;
}

#more-query-template p {
    color: white;
    font-family: 'Poppins', sans-serif;
    font-size: 1.8rem;
    text-align: center;
    /* margin-bottom: 1rem; */
    text-shadow: 0 0 5px black;
}

#more-query-template span {
    color: yellowgreen;
    text-shadow: 0 0 5px red;
}