.about {
    padding-top: 30px;
    font-family: 'Poppins', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: linear-gradient(to right, rgb(169, 203, 241), rgb(73, 149, 237));
    height: auto;
}


.about h1 {
    font-size: 2.5rem;
    text-shadow: 0 0 5px rgb(155, 157, 175);
    padding-top: 1rem;
    color: blue;

}

.about h4 {
    text-shadow: 0 0 5px rgb(155, 157, 175);
    font-size: 1.1rem;
    padding-bottom: 2rem;
}

#aboutme img {
    height: 40rem;
    border-radius: 100rem;
}

.content-area {
    display: grid;
    grid-template-columns: 1.5fr 2fr;
    margin-bottom: 5rem;
}

.about-content {
    padding: 10px 100px 0 30px;
    color: white;
    text-shadow: 0 0 5px black;
    font-size: 1.1rem;
}

.about-content h3 {
    font-size: 1.7rem;
    text-shadow: 0 0 5px rgb(155, 157, 175);
    padding: 2rem;
    color: rgba(255, 255, 255, 0.851);
    text-align: center;
}

.about-content span {
    color: blueviolet;
    font-size: 2.3rem;
    padding: 12px;
}

.content-area img {
    padding-inline: 20px;
    align-self: center;
    justify-self: center;
}


@media (max-width:800px) {
    .content-area {
        display: grid;
        grid-template-columns: auto;
        grid-template-rows: auto auto;
    }

    .about img {
        height: 30rem;
        border-radius: 100rem;
    }

    .about-content {
        padding: 10px 50px;
        color: white;
        text-shadow: 0 0 5px black;
    }

    .about h1 {
        font-size: 2rem;
    }

    .about h4 {
        font-size: 1rem;
    }

    #aboutme img {
        height: 30rem;
    }

    .about-content h3 {
        font-size: 1.1rem;
    }

    .about-content span {
        font-size: 1.4rem;
    }

    .about-content {
        font-size: 14px;
    }
}