.form {
    font-family: 'Poppins', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form h1 {
    font-size: 2.5rem;
    text-shadow: 0 0 5px rgb(155, 157, 175);
    padding-top: 1rem;
    color: blue;

}

.form h4 {
    text-shadow: 0 0 5px rgb(155, 157, 175);
    font-size: 1.1rem;
    padding-bottom: 2rem;
}

.apply-form {
    background-color: white;
    width: 35rem;
    height: auto;
    border: none;
    border-style: inset;
    border-color: rgb(0, 98, 255);
    border-width: 5px;
    border-radius: 2rem;
    box-shadow: 5px 5px 15px black;
    display: flex;
    flex-direction: column;
    padding: 12px;
}

.form::before {
    content: '';
    position: absolute;
    background-image: url(https://st2.depositphotos.com/1594308/12210/i/600/depositphotos_122104490-stock-photo-smiing-female-college-student.jpg);
    width: 100vw;
    height: 125vh;
    opacity: .9;
    filter: blur(2px);
    background-position: center;
    background-size: cover;
    z-index: -1;
}



.apply-form h2 {
    font-size: 2rem;
    color: rgb(21, 118, 245);
    align-self: center;
}

