.apply-form label {
    margin: 1rem 0 .2rem 0;
    font-size: 1.1rem;
    color: rgb(0, 106, 255);
}

.apply-form input {
    height: 2rem;
    font-size: 1.3rem;
    border: 1px solid blue;
    border-radius: 5px;
    padding-inline: 1rem;
}

.apply-form select {
    font-size: 1.2rem;
    color: rgb(87, 83, 83);
    border: 1px solid blue;
    border-radius: 5px;
    padding-inline: 1rem;
}

.apply-form ul {
    list-style: none;
    color: rgb(255, 13, 0);
    margin: 2rem 0;
}

@media (max-width:600px) {
    .apply-form {
        width: 22rem;
        padding: 12px;
    }

}

@media (max-width:400px) {
    .apply-form {
        width: 18rem;
        padding: 12px;
    }

}

.btn-close {
    position: absolute;
    left: 8px;
    top: 8px;
    font-size: 1.4rem;
    padding: 6px 12px;
    border-radius: 3rem;
}

textarea {
    font-size: 1.3rem;
    border: 1px solid blue;
    border-radius: 5px;
    padding: 1rem;
}

.hidden {
    display: none;
}