#bottom-sec li {
    list-style: none;
}


#bottom-sec {
    display: flex;
    flex-direction: row;
    font-size: 1.1rem;
    width: 100vw;
    height: auto;
    color: white;
    padding: 6rem 0;
    margin-inline: auto;
    justify-content: center;
    font-family: 'Poppins', sans-serif;
    background-image: linear-gradient(to right, rgb(133, 134, 134), rgb(146, 177, 239));
}

#bottom-sec a {
    text-decoration: none;
    cursor: pointer;
    color: white;
}

#bottom-sec a:hover {
    color: blue;
}

#bottom-sec li:hover {
    color: blue;
    cursor: pointer;
}

.location {
    padding-inline: 8rem;
}

#bottom-sec h5 {
    font-size: 1.5rem;
    color: blue;
    margin: 1rem 0;
}

.follow-us span {
    font-size: 1.7rem;
    padding-right: 2rem;
}

@media (max-width: 800px) {
    #bottom-sec {
        display: flex;
        flex-direction: column;
        font-size: 14px;
        padding: 3rem 0;
    }

    #bottom-sec div {
        padding-inline: 30vw;
    }
}